import { OpenHours } from '@nylas/core';
import moment from 'moment-timezone';

export const LANGUAGE_MAP: {
  [key: Navigator['language']]: string;
} = {
  'en-US': 'English',
};

export const TIMEZONE_MAP: {
  [key: string]: string;
} = (() => {
  // List of all time zone names known to the Intl API
  const timeZones = moment.tz.names();

  return timeZones.reduce((acc: { [key: string]: string }, timeZone: string) => {
    acc[timeZone] = timeZone.replace(/\//g, ' / ');
    acc[timeZone] = acc[timeZone].replace(/_/g, ' ');
    return acc;
  }, {});
})();

export function getTimezoneOffset(timezone: string): string {
  return moment.tz(timezone).format('Z');
}

